  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* 5.2 Menus
--------------------------------------------------------------*/
.main-navigation {
  clear: both;
  display: block;
  float: left;
  width: 100%; }
  .main-navigation ul {
    list-style: none;
    margin: 0;
    padding-left: 0;
    /*
		 * Submenus.
		 * 1. Override the default sub-list styling.
		 * 2. Delay moving the menu off-screen until it's faded out.
		 */
    /*
		 * Show submenu on parent:hover.
		 * 1. For fade in effect.
		 * 2. Overwrite the delay in moving the submenu into view, so we can see
		 *    the fade in effect.
		 */ }
    .main-navigation ul ul {
      top: 25px;
      top: 1.38889rem;
      font-size: 18px;
      font-size: 1rem;
      /* 1 */
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      float: left;
      position: absolute;
      left: -999em;
      z-index: 99999;
      min-width: 100%;
      opacity: 0;
      line-height: 1.25;
      /* 1 */
      transition: 0.2s opacity ease-in, 0s left linear 0.2s;
      /* 2 */ }
      .main-navigation ul ul li {
        padding: 6.25px 12.5px;
        padding: 0.34722rem 0.69444rem;
        text-align: left; }
      .main-navigation ul ul a {
        width: 200px; }
    .main-navigation ul li:hover > ul {
      left: 0;
      opacity: 1;
      /* 1 */
      transition: 0.2s opacity ease-in;
      /* 2 */ }
  .main-navigation li {
    position: relative; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/* Small menu. */
.menu-toggle, .main-navigation.toggled .nav-menu {
  display: block; }

.main-navigation .nav-menu {
  display: none; }

/* Responsive menu */
@media screen and (min-width: 45em) {
  .menu-toggle {
    display: none; }
  .main-navigation .nav-menu {
    display: table;
    width: 100%; }
    .main-navigation .nav-menu > li {
      display: table-cell;
      text-align: center;
      border-right: 1px solid #fff;
      border-left: 1px solid #fff;
      background: #eee; } }

.site-main .comment-navigation, .site-main .posts-navigation, .site-main .post-navigation {
  margin: 0 0 25px;
  margin: 0 0 1.38889rem;
