html {
	box-sizing: border-box;
}

/*
 * Inherit box-sizing to make it easier to change the property for components
 * that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
 */
*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background: $background-color__body; /* Fallback for when there is no custom background color defined. */
}

blockquote,
q {
	quotes: "" "";

	&:before,
	&:after {
		content: "";
	}

}

blockquote {
	@include font-size-line( "large" );
	font-style: italic;
	font-weight: bold;
	font-family: $font-family__blockquote;
}

cite {
	font-style: normal;
	font-weight: bold;
	font-size: 0.9em;
	font-family: $font-family__cite;
	text-transform: uppercase;
	letter-spacing: 0.05em;
}

hr {
	background-color: $background-color__hr;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

img {
	height: auto; /* Make sure images are scaled correctly. */
	max-width: 100%; /* Adhere to container width. */
}
